.main-navigation {
  position: relative;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.3);

  @media (max-width: $tablet-width-only) {
    &--opened,
    &--nojs {
      background-color: $primary-color;
    }
  }
}

.main-navigation__container {
  display: flex;

  @media (max-width: $tablet-width-only) {
    flex-direction: column;
  }

  @media (min-width: $desktop-width) {
    padding: 0 10px;
    align-items: center;
  }
}

.main-navigation__wrapper {
  padding: 23px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $tablet-width) {
    padding-top: 27px;
    padding-bottom: 43px;
  }

  @media (min-width: $desktop-width) {
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
  }
}

.main-navigation__logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-navigation__toggle {
  display: flex;
  padding: 0;
  border: none;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  background-repeat: no-repeat;
  fill: #ffffff;
  cursor: pointer;
  transition: fill 0.3s;

  &:hover,
  &:focus {
    fill: $accent-color;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin-top: 15px;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

@media (max-width: $tablet-width-only) {
  .main-navigation--opened .main-navigation__list {
    display: flex;
  }

  .main-navigation--closed .main-navigation__list {
    display: none;
  }

  .main-navigation--closed .main-navigation__toggle--close,
  .main-navigation--opened .main-navigation__toggle--open {
    display: none;
  }

  .main-navigation--nojs .main-navigation__toggle {
    display: none;
  }

  .main-navigation--nojs .main-navigation__list {
    display: flex;
  }
}
